<template>
	<nav-bar id="detail-navbar">
		<template #left>
			<img src="../../../assets/img/common/back.svg" alt class="backbtn" @click="backBtnClick" />
		</template>
		<template #center>
			<span
				v-for="(item,index) in title"
				:key="item.name"
				class="title-item"
				:class="{active:index===currentIndex}"
				@click="titleClick(index)"
			>{{item}}</span>
		</template>
	</nav-bar>
</template>

<script>
import NavBar from '../../../components/common/navbar/NavBar';

export default {
	components: {
		NavBar,
	},
	data() {
		return {
			title: ['商品', '参数', '评论', '推荐'],
			currentIndex: 0,
		};
	},
	methods: {
		titleClick(index) {
			this.currentIndex = index;
			this.$emit('titleClick', index);
		},
		backBtnClick() {
			this.$router.go(-1);
		},
	},
};
</script>

