<template>
	<div id="detail">
		<detail-nav-bar @titleClick="titleClick" ref="navbar"></detail-nav-bar>

		<scroll class="detail-content" ref="detailscroll" @scrollListener="scrollListener" :probetype="3">
			<template>
				<detail-swiper :top-images="topImages"></detail-swiper>
				<detail-base-info :goods="goods" ref="goods"></detail-base-info>
				<detail-shop-info :shop="shop" ref="shop"></detail-shop-info>
				<detail-goods-info :detailInfo="detailInfo" ref="detailInfo"></detail-goods-info>
				<detail-param-info :paramInfo="paramInfo" ref="paramInfo"></detail-param-info>
				<detail-comment-info :commentInfo="commentInfo" ref="commentInfo"></detail-comment-info>
				<recommend-list :goods="recommends" ref="recommends"></recommend-list>
			</template>
		</scroll>
		<detail-bottom-bar @addToCart="addToCart"></detail-bottom-bar>
		<toast ref="toast"></toast>
	</div>
</template>

<script>
import DetailNavBar from '../datail/ChildComponent/detailnavbar';
import {
	getDetail,
	Goods,
	Shop,
	GoodsParam,
	getRecommend,
} from '../../network/detail';
import DetailSwiper from '../datail/ChildComponent/detailswiper';
import DetailBaseInfo from '../datail/ChildComponent/DetailBaseInfo';
import DetailShopInfo from '../datail/ChildComponent/DetailShopInfo';
import Scroll from '../../components/common/scroll/scroll';
import { debounce } from '../../common/utils/utils';
import DetailGoodsInfo from '../datail/ChildComponent/DetailGoodsInfo';
import DetailParamInfo from '../datail/ChildComponent/DetailParamInfo';
import DetailCommentInfo from '../datail/ChildComponent/DetailCommentInfo';
import RecommendList from '../../components/content/goods/GoodsList';
import DetailBottomBar from '../datail/ChildComponent/DetailBottomBar';
import Toast from '../../components/common/toast/toast';

export default {
	name: 'Detail',
	data() {
		return {
			iid: null,
			topImages: [],
			goods: {},
			shop: {},
			detailInfo: {},
			paramInfo: {},
			commentInfo: {},
			recommends: [],
			titleTopY: [],
			currentIndex: 0,
		};
	},
	components: {
		DetailNavBar,
		DetailSwiper,
		DetailBaseInfo,
		DetailShopInfo,
		Scroll,
		DetailGoodsInfo,
		DetailParamInfo,
		DetailCommentInfo,
		RecommendList,
		DetailBottomBar,
		Toast,
	},
	created() {
		this.iid = this.$route.params.iid;
		//获取商品数据
		getDetail(this.iid).then((res) => {
			// 获取顶部图片数据
			this.topImages = res.result.itemInfo.topImages;
			// 获取商品信息
			this.goods = new Goods(
				res.result.itemInfo,
				res.result.columns,
				res.result.shopInfo.services
			);
			//获取店铺信息
			this.shop = new Shop(res.result.shopInfo);
			//获取商品详细信息
			this.detailInfo = res.result.detailInfo;
			//获取商品参数
			this.paramInfo = new GoodsParam(
				res.result.itemParams.info,
				res.result.itemParams.rule
			);
			//获取评论信息
			if (res.result.cRate !== 0) {
				this.commentInfo = res.result.rate.list[0];
			}
		});
		//获取推荐数据
		getRecommend().then((res) => {
			this.recommends = res.data.list;
		});
	},
	mounted() {
		const refresh = debounce(this.$refs.detailscroll.refresh, 500);
		const getTitleTop = debounce(() => {
			this.titleTopY = [];
			this.titleTopY.push(0);
			this.titleTopY.push(this.$refs.paramInfo.$el.offsetTop);
			this.titleTopY.push(this.$refs.commentInfo.$el.offsetTop);
			this.titleTopY.push(this.$refs.recommends.$el.offsetTop);
			this.titleTopY.push(Number.MAX_VALUE);
		}, 500);
		this.$bus.$on('detailSwiperLoad', () => {
			refresh();
		});
		this.$bus.$on('detailImgLoad', () => {
			refresh();
			getTitleTop();
		});
	},
	methods: {
		titleClick(index) {
			this.$refs.detailscroll.scrollTo(
				0,
				-this.titleTopY[index] + 44,
				1000
			);
		},
		scrollListener(pos) {
			const positionY = -pos.y;
			let length = this.titleTopY.length;
			for (let i = 0; i < length - 1; i++) {
				// if (
				// 	(i < length - 1 &&
				// 		positionY >= this.titleTopY[i] &&
				// 		positionY < this.titleTopY[i + 1]) ||
				// 	(i === length - 1 && positionY >= this.titleTopY[i])
				// ) {
				// 	this.currentIndex = i;
				// 	console.log(this.currentIndex);
				// }
				if (
					this.currentIndex !== i &&
					positionY >= this.titleTopY[i] - 44 &&
					positionY < this.titleTopY[i + 1] - 44
				) {
					this.currentIndex = i;
					this.$refs.navbar.currentIndex = this.currentIndex;
				}
			}
		},
		addToCart() {
			const goods = {};
			goods.image = this.topImages[0];
			goods.title = this.goods.title;
			goods.desc = this.goods.desc;
			goods.price = this.goods.nowPrice;
			goods.iid = this.iid;
			this.$store.dispatch('addCart', goods);
			this.$refs.toast.toast('已加入购物车');
		},
	},
};
</script>

<style>
#detail {
	position: relative;
	z-index: 3;
	background-color: #fff;
	height: 100vh;
}
#detail .center {
	background-color: #fff;
	display: flex;
}
#detail .left {
	background-color: #fff;
}
#detail .right {
	background-color: #fff;
}

#detail .title-item {
	flex: 1;
}
#detail .active {
	color: #e6335f;
}
#detail .backbtn {
	margin-top: 10px;
	margin-left: -30px;
}

.detail-content {
	height: calc(100% - 102px);
	overflow: hidden;
}
</style>