<template>
	<div id="detail-swiper">
		<swiper class="detailswiper">
			<swiper-item v-for="item in topImages" :key="item.name" class="detail-swiper-item">
				<img :src="item" alt @load="imageload" />
			</swiper-item>
		</swiper>
	</div>
</template>

<script>
import { Swiper, SwiperItem } from '../../../components/common/swiper/';

export default {
	components: {
		Swiper,
		SwiperItem,
	},
	props: {
		topImages: Array,
	},
	methods: {
		imageload() {
			this.$bus.$emit('detailSwiperLoad');
		},
	},
};
</script>

<style>
.detailswiper {
	height: 300px;
}
</style>